body {
  background-color: #d3d3d3;
}
.brand-logo {
  margin-left: 10px;
}

.text-wrapper {
  margin-left: 10px;
  font-size: 2rem;
  font-family: 'Helvetica Neue', Helvetica, Arial, sans-serif;
  text-align: center;
}

#time {
  font-size: 500%;
  letter-spacing: -4px;
  font-weight: 500;
  margin-top: 30px;
}

#message {
  font-weight: 500;
  padding: 0;
  margin-top: -10%;
  font-size: 3rem;
}
h4 {
  font-size: 1.3rem;
}
body {
  background-color: #eee;
  background-image: url('https://www.transparenttextures.com/patterns/arabesque.png');
}

.App {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  min-height: 90vh;
  -webkit-align-items: center;
          align-items: center;
  -webkit-flex-direction: column;
          flex-direction: column;
}
.quoteBox {
  background-color: #dbbf09;
  background-image: url('https://www.transparenttextures.com/patterns/crisp-paper-ruffles.png');
  background-size: cover;
  width: 450px;
  height: 320px;
  border-radius: 10px;
  padding: 50px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  -webkit-flex-direction: column;
          flex-direction: column;
}
#quote {
  font-family: 'Lobster Two', cursive;
  margin: 0;
  font-size: 1.3rem;
  margin-top: 10px;
  color: #fff;
}
#author {
  font-family: 'Dancing Script', cursive;
  margin: 15px 0;
  text-align: center;
  font-size: 1.3rem;
  height: 1vh;
  font-weight: 600;
  color: #fff;
}
.btn {
  display: block;
  border: 2px solid #4caf50;
  background: transparent;
  color: #008cba;

  font-size: 1.2rem;
  font-weight: 500;
  border-radius: 5px;
  cursor: pointer;
  outline: none;
  transition: background 1s, color 1s;

  margin-top: 3%;
  margin-bottom: 5%;
}
.btn:hover {
  background: #4caf50;
  color: #fff;
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

body {
  padding: 0;
  margin: 0;
}
.title {
  text-align: center;
  font-size: 30px;
  color: #4caf50;
  font-weight: 700;
  margin-bottom: -50px;
}
.app {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  width: 100vw;
  height: 100vh;
}
.grid {
  display: grid;
  grid-template-columns: auto auto auto;
  grid-template-rows: auto auto auto;
  grid-column-gap: 10px;
  -webkit-column-gap: 10px;
          column-gap: 10px;
  grid-row-gap: 10px;
  row-gap: 10px;
  background-color: #4caf50;
}

.square {
  width: 110px;
  height: 110px;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  background-color: #e7e7e7;
}

svg {
  stroke-width: 15px;
  stroke: #ff665c;
  fill: none;
  stroke-linecap: round;
}

.cross {
  stroke: #ec0b8e;
}

.result {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-flex-direction: column;
          flex-direction: column;
  font-size: 3em;
  font-weight: bold;
  background-color: rgba(0, 0, 0, 0.4);
  color: white;
}

button {
  padding: 10px 20px;
  margin: 10px;
  font-size: 0.5em;
  font-weight: bold;
  border-radius: 10px;
  color: gray;
}


